
import Capa from './Components/Capa';
import Pag02 from './Components/Pag02';
import ComoFunciona from './Components/ComoFunciona';
import Header from './Components/Header';
import Footer from './Components/Footer';
import Portifolio from './Components/Portifolio'
import './Style/App.css';
import { useState} from 'react';
import PrecoDesign from './Components/PrecoDesign';
import PrecoArquitetonico from './Components/PrecoArquitetonico';

function App() {
  
  const[alternar, setAlternar] = useState(true)
  const exibir = (props)=>{
    setAlternar(props)
  }
  const scrolling = ()=>{  
    let altura = window.screen.height*.75;
    window.scrollTo(0, altura)
    console.log(altura)    
  }
  return (
    <div className="App" >
    <Header exibir={exibir}/>
    
    {alternar &&
      <>
      <Capa scrolling={scrolling}/>
      <ComoFunciona/>
      <PrecoDesign/>
      <PrecoArquitetonico/>
      </>
    }
    {!alternar &&
      <>
      <Portifolio />
      </>
    }
    <Footer/> 
    
    </div>
    );
  }
  
  export default App;
  