import React from 'react';
import '../Style/Preco.css'

const PrecoDesign = () =>
{    
    const vBaseBasico = 599
    const vVistaBasico = parseFloat(vBaseBasico).toFixed(2).toString().replace(".", ",")
    const vPrazoBasico = vBaseBasico*1.15
    const divididoBasico = 5
    const vDivididoBasico = parseFloat(vPrazoBasico/divididoBasico).toFixed(2).toString().replace(".", ",")
    
    const vBaseCompleto = 899
    const vVistaCompleto = parseFloat(vBaseCompleto).toFixed(2).toString().replace(".", ",")
    const vPrazoCompleto = vBaseCompleto*1.15
    const divididoCompleto = 8
    const vDivididoCompleto = parseFloat(vPrazoCompleto/divididoCompleto).toFixed(2).toString().replace(".", ",")
    return(
        <div className='Preco'>            
            <div className='card--preco'>
                <h1>Design</h1>
                <div className='card--conteiner'>
                    <div className='card'>
                        <h2>Basico</h2>
                        <br></br> 
                        <h4>{divididoBasico}x de R$ {vDivididoBasico}</h4>
                        <h4>ou R$ {vVistaBasico} à vista por ambiente*</h4>            
                        <ol>
                        <li><img src={'./icons/v.png'} alt=''/> Lista de materiais de acabamento</li>
                        <li><img src={'./icons/x.png'} alt=''/> Móveis (detalhamento de planejados)</li>
                        <li><img src={'./icons/v.png'} alt=''/> Planta baixa e executiva do ambiente </li>
                        <li><img src={'./icons/v.png'} alt=''/> 02 Imagens 3D realistas </li>
                        <li><img src={'./icons/v.png'} alt=''/> Suporte na leitura do projeto</li>
                        <li><img src={'./icons/v.png'} alt=''/> 02 pedidos de revisão</li>
                        </ol>
                        <h5>* Considera-se um ambiente tendo até 25m². Caso seja maior, considerar 2 ou mais ambientes.</h5> 
                        <br></br> 
                        <div className='card--footer'>
                            <h4>Desconto por ambiente adicionado:</h4>
                            <h4>- 4% para 02 ambientes </h4>
                            <h4>- 8% para 03 ambientes</h4>
                            <h4>- 12% para 04 e mais ambientes</h4>
                        </div>
                    </div>
                        
                    <div className='card'>
                        <h2>Completo</h2>
                        <br></br> 
                        <h4>{divididoCompleto}x de R$ {vDivididoCompleto}</h4>
                        <h4>ou R$ {vVistaCompleto} à vista por ambiente*</h4>            
                        <ol>
                        <li><img src={'./icons/v.png'} alt=''/> Lista de materiais de acabamento</li>
                        <li><img src={'./icons/v.png'} alt=''/> Móveis (detalhamento de planejados)</li>
                        <li><img src={'./icons/v.png'} alt=''/> Planta baixa e executiva do ambiente </li>
                        <li><img src={'./icons/v.png'} alt=''/> 02 Imagens 3D realistas </li>
                        <li><img src={'./icons/v.png'} alt=''/> Suporte na leitura do projeto</li>                        
                        <li><img src={'./icons/v.png'} alt=''/> 03 pedidos de revisão</li>
                        </ol>
                        <h5>* Considera-se um ambiente tendo até 25m². Caso seja maior, considerar 2 ou mais ambientes.</h5> 
                        <br></br> 
                        <div className='card-footer'>
                        <h4>Desconto por ambiente adicionado:</h4>
                        <h4>- 4% para 02 ambientes </h4>
                        <h4>- 8% para 03 ambientes</h4>
                        <h4>- 12% para 04 e mais ambientes</h4>
                        </div>
                    </div>                
                </div> 
            </div>            
        </div>
        )    
    }
    export default PrecoDesign;